#primary-menu {
  .menu-item-has-children {
    position: relative;
    padding-right: 1rem;
    &::before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 15%;
    right: 23px;
    z-index: 2;
    content: "";
    width: 0;
    height: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 5px solid black;
    cursor: pointer;
    margin-left: 4px;
    transition: 300ms;
    }
    a {
      padding-right: 1rem;
    }
    .sub-menu {
      opacity: 0;
      visibility: hidden;
      margin-top: 1rem;
      transition: all 300ms ease-in;
    }
    .show-dropdown {
      opacity: 1;
      visibility: visible;
    }
  }
}


