footer {
  margin-top: 8rem;
}

.site-footer {
  position: relative;
  &::after {
    content: '';
    background-color: $brown-2;
    width: 25%;
    height: 1.8px;
    display: block;
    position: absolute;
    top: -25px;
    left: 50%;
    transform: translate(-50% , 50%);
  }
}

.white-footer {
  position: relative;
  margin-top: 13rem;
  &::before {
    content: '';
    background-color: $white !important;
    width: 25%;
    height: 1.8px;
    display: block;
    position: absolute;
    top: -45px;
    left: 50%;
    transform: translate(-50% , 50%);
  }
}

.copyright-text, .laws-and-codes {
  font-family: $monseratt;
  font-size: 1rem;
  line-height: 18px;
  text-align: center;
  margin: 1.1rem 0;
  display: block;
  @media only screen and (max-width: 800px) {
    margin-top: 5rem;
    font-size: 0.8rem;
  }
  a {
    font-family: $monseratt;
    color: $white;
    transition: all 300ms ease-in;
    &:hover {
      color: $brown-2;
    }
  }
}

.laws-and-codes {
  @media only screen and (max-width: 800px) {
    margin-top: 0rem;
    font-size: 0.8rem;
  }
}

.footer-nav {
  ul {
    display: flex;
    justify-content: center;
    list-style: none;
    margin-top: 1.5rem;
    @media only screen and (max-width: 800px) {
      margin-top: 0rem;
    }
    li a {
      font-family: $monseratt;
      font-size: 1rem;
      line-height: 0.9;
      color: $white;
      text-decoration: underline;
      text-underline-position: under;
      @media only screen and (max-width: 800px) {
        font-size: 0.9rem;
      }
      &:hover {
        transition: all 300ms ease-in;
        color: $black;
      }
    }
  }
}

.footer-bottom-links {
  display: flex;
  justify-content: space-between;
  margin-top: 3.5rem;
  margin-left: 2.5rem;
  margin-right: 2.5rem;

  @media only screen and (max-width: 800px) {
    margin-top: 2.5rem;
    font-size: 0.8rem;
    margin-left: 1rem;
    margin-right: 1rem;
  }
}


